import React from "react"
import Layout from "../components/layout";
import { graphql, useStaticQuery, Link } from "gatsby";

const ArchivePage = () => {
  const data = useStaticQuery(graphql`
      query {
  allMarkdownRemark(
    sort: { fields: [frontmatter___date], order: DESC }
  ) {
    edges {
      node {
        frontmatter {
          title
          formattedDate: date(formatString: "MMMM Do, YYYY")
          date
          slug
        }
        html
        excerpt
        fields {
          slug
        }
      }
    }
  }
}`);

  return <Layout>
    <h1>Archives</h1>
    <ul>
      {data.allMarkdownRemark.edges.map(({ node }, index) => {
        const pattern = /\d{4}\-(0?[1-9]|1[012])\-(0?[1-9]|[12][0-9]|3[01])-/gm;
        const newslug = node.fields.slug.match(pattern).join().replace(/\-/g, "/").slice(0, -1);
        return (<li key={index}>
          <span>
            <Link to={`archive/${newslug}/${node.frontmatter.slug}`}>
              {node.frontmatter.title}
            </Link>
            <time style={{ marginLeft: 5 }}>{node.frontmatter.formattedDate}</time>
          </span>
        </li>);
      })}
    </ul>
  </Layout>
};

export default ArchivePage;